                        </template>
                        <template #message>
                            Для автора очень важна Ваша поддержка. Когда спонсорство будет подходить к завершению, то мы пришлем Вам личное сообщение с напоминанием об этом
                        </template>
                    </BaseCheckbox>
                </BaseFormRow>
                <BaseFormRow>
                    <BaseCheckbox
                        checked
