                        Для того, чтобы автор продолжал заниматься творчеством, ему необходима Ваша своевременная поддержка.
                        Когда Ваш период спонсорства будет подходить к концу, то мы пришлем Вам личное сообщение с напоминанием об этом, чтобы Вы вновь смогли поддержать автора.
                    </p>
                    <p>
                        Тем не менее, если Вы все равно не хотите получать напоминания, то Вы можете подтвердить это ниже.
                    </p>
                </BaseFormRow>
                <div class="form__actions">
                    <BaseButton
                        label="Напоминать"
                        @click="onModalNotifyChoose(true)"
                    />
                    <BaseButton
                        label="Не напоминать"
                        type="tertiary"
                        @click="onModalNotifyChoose(false)"
                    />
