</template>

<script>
import ElementCard from '../components/ElementCard';
import ElementAvatar from '../components/ElementAvatar';
import ElementPlaceholder from '../components/ElementPlaceholder';
import ElementItemAwards from '../components/ElementItemAwards';
import ElementItem from '../components/ElementItem';
import SelectInline from '../components/SelectInline';

export default {
    name: 'Showcase',
    components: {
        SelectInline, ElementItem, ElementItemAwards, ElementPlaceholder, ElementAvatar, ElementCard,
    },

    data() {
        return {
            modalStatus: false,
            modalStatusSelected: null,

            modalPayment: false,
            modalPaymentPayPeriod: 1,

            modalNotify: false,

            cart: [3, 4],

            statuses: [
                {
                    id: 1,
                    type: 1,
                    name: 'Silver',
                    description: 'Описание уровня',
                    cost: 100,
                    sponsors: 100,
                },
                {
                    id: 2,
                    type: 1,
                    name: 'Gold',
                    description: '[url=https://ya.ru]ya.ru[/url] https://momentjs.com/dasdasd Задача организации, в особенности же начало повседневной работы по формированию позиции позволяет оценить значение новых предложений. Повседневная практика показывает, что консультация с широким активом обеспечивает широкому кругу (специалистов) участие в формировании системы обучения кадров, соответствует насущным потребностям.',
                    cost: 1000,
                    sponsors: 10,
                },
                {
                    id: 3,
                    type: 2,
                    name: '1',
                    description: 'Описание подписки',
                    cost: 100,
                    sponsors: 100,
                    is_lifetime: false,
                },
                {
                    id: 4,
                    type: 2,
                    name: '2',
                    description: 'Описание подписки',
                    cost: 1000,
                    sponsors: 1000,
                    is_lifetime: true,
                },
            ],

            goals: [
                {
                    id: 1,
                    type: 1,
                    name: 'На певко деньги',
                    description: 'Это моя цель',
                    date_end: Date.now() + 1000 * 60 * 60 * 24 * 10,
                    funded: 1000,
                    sponsors: 100,
                    goal: 10000,
                },
                {
                    id: 2,
                    type: 2,
                    name: 'На певко спонсоры',
                    description: 'Задача организации, в особенности же начало повседневной работы по формированию позиции позволяет оценить значение новых предложений. Повседневная практика показывает, что консультация с широким активом обеспечивает широкому кругу (специалистов) участие в формировании системы обучения кадров, соответствует насущным потребностям.',
                    date_end: Date.now() + 1000 * 60 * 60 * 24 * 100,
                    funded: 1000,
                    sponsors: 1000,
                    goal: 100,
                },
            ],
        };
    },

    computed: {
        levels() {
            return this.statuses.filter((status) => status.type == 1);
        },

        subscribes() {
            return this.statuses.filter((status) => status.type == 2);
        },

        statusesMap() {
            const map = {};
            this.statuses.forEach((status) => map[status.id] = status);

            return map;
        },

        statusesInCart() {
            const statuses = {
                level: null,
                subscribes: [],
            };

            this.cart.forEach((statusId) => {
                if (this.statusesMap[statusId].type == 1) {
                    statuses.level = this.statusesMap[statusId];
                    return;
                }

                statuses.subscribes.push(this.statusesMap[statusId]);
            });

            return statuses;
        },

        statusesInCartIsLifetimeOnly() {
            return !this.statusesInCart.level && !this.statusesInCart.subscribes.find((subscribe) => !subscribe.is_lifetime);
        },

        cartCost() {
            const periodMultiply = {
                1: 1,
                2: 3,
                3: 6,
                4: 12,
            };

            let cost = 0;

            if (this.statusesInCart.level) {
                cost += this.statusesInCart.level.cost * periodMultiply[this.modalPaymentPayPeriod];
            }

            this.statusesInCart.subscribes.forEach((subscribe) => {
                cost += subscribe.cost * (subscribe.is_lifetime ? 1 : periodMultiply[this.modalPaymentPayPeriod]);
            });

            return cost;
        },
    },

    watch: {
        modalPayment() {
            this.modalPaymentPayPeriod = 1;
        },
    },

    mounted() {
        this.computeSizes(this.$refs.levels);
        this.computeSizes(this.$refs.subscribes);
    },

    methods: {
        computeSizes(parent) {
            if (this.$device.isMobile() || !parent) return;

            this.$nextTick(() => {
                const $levels = [...parent.querySelectorAll('.js-level')];
                const maxHeight = $levels.reduce((previousValue, level) => (level.offsetHeight > previousValue ? level.offsetHeight : previousValue), 0);

                parent.querySelector('.js-wrapper').style.height = `${$levels.length * maxHeight}px`;

                const start = $levels[0].getBoundingClientRect().top;
                let end = $levels[$levels.length - 1].getBoundingClientRect().bottom;

                if ($levels.length >= 2) {
                    const beforeEnd = $levels[$levels.length - 2].getBoundingClientRect().bottom;
                    end = end > beforeEnd ? end : beforeEnd;
                }

                parent.style.height = `${Math.ceil(end - start) + 20 * 2}px`;
            });
        },

        getComponentProgressData(goal) {
            if (goal.type == 1) {
                return {
                    value: goal.funded / goal.goal * 100,
                    labelStart: this.$format.money(goal.funded),
                    labelFinish: this.$format.money(goal.goal),
                };
            }

            return {
                value: goal.sponsors / goal.goal * 100,
                labelStart: `${this.$format.number(goal.sponsors)} ${this.$format.declensionWord(goal.sponsors, 'спонсор', 'спонсора', 'спонсоров')}`,
                labelFinish: `${this.$format.number(goal.goal)} ${this.$format.declensionWord(goal.goal, 'спонсор', 'спонсора', 'спонсоров')}`,
            };
        },

        showModalStatus(status) {
            this.modalStatusSelected = status;
            this.modalStatus = true;
        },

        addToCart(status) {
            if (status.type == 1) {
                const index = this.cart.findIndex((statusId) => this.statusesMap[statusId].type == 1);

                if (index != -1) {
                    this.cart.splice(index, 1);
                }
            }

            this.cart.push(status.id);
            this.modalStatus = false;
        },

        removeFromCart(statusId) {
            this.modalStatus = false;

            const index = this.cart.findIndex((sId) => sId == statusId);

            if (index != -1) {
                this.cart.splice(index, 1);
            }

            if (this.cart.length === 0) {
                this.modalPayment = false;
            }
        },

        onNotifyChange(checked) {
            if (checked) return;

            this.$refs.notifyOption.setChecked(true);
            this.modalNotify = true;
        },

        onModalNotifyChoose(value) {
            this.$refs.notifyOption.setChecked(value);
            this.modalNotify = false;
        },
    },
};
</script>

