<template>
    <div class="select-inline">
        <div
            v-if="label"
            class="select-inline__label"
        >
            <BaseLabel
                :text="label"
            />
        </div>
        <div class="select-inline__options">
            <label
                v-for="option in options"
                class="select-inline__option"
            >
                <input
                    type="checkbox"
                    class="select-inline__option-input sr-only"
                    :checked="isSelected(option.value)"

                    @change="onSelect($event, option.value)"
                >
                <BaseButton
                    size="small"
                    :type="isSelected(option.value) ? 'primary' : 'secondary'"
                    class="select-inline__option-element"
                    :class="{ 'select-inline__option-element_selected': isSelected(option.value) }"
                >
                    {{ option.text }}
                </BaseButton>
            </label>
        </div>
        <input
            type="hidden"
            :name="name"
            :value="selected_.join(',')"
        >
        <BaseMessage
            v-if="$slots.message"
            class="select-inline__message"
        >
            <slot name="message"/>
        </BaseMessage>
    </div>
</template>

<script>
export default {
    name: 'SelectInline',

    props: {
        label: String,
        options: Array,
        multiple: Boolean,
        name: String,

        selected: {
            type: [String, Number, Array],
            default: () => [],
        },
    },

    data() {
        return {
            selected_: Array.isArray(this.selected) ? [...this.selected] : [this.selected],
        };
    },

    methods: {
        isSelected(value) {
            return this.selected_.find((val) => val == value);
        },

        onSelect(e, value) {
            if (e.target.checked) {
                if (this.multiple) {
                    this.selected_.push(value);
                } else {
                    this.selected_ = [value];
                }

                this.$emit('select', value);
                return;
            }

            if (this.selected_.length === 1) {
                e.preventDefault();
                return;
            }

            const index = this.selected_.findIndex((val) => val == value);

            if (index != -1) {
                this.selected_.splice(index, 1);
            }
        },
    },
};
</script>

<style scoped lang="less">
    .select-inline {
        /deep/ &__label {
            margin-bottom: 0.4rem;
        }

        /deep/ &__message {
            margin-top: 0.4rem;
        }

        &__options {
            display: flex;
            margin: -5px 0 0 -5px;
        }

        /deep/ &__option {
            display: block;
            margin: 5px 0 0 5px;

            &-input:focus + &-element {
                background: var(--ui__button_secondary_background_active);
            }

            &-input:focus + &-element_selected {
                background: var(--ui__button_primary_background_active);
            }
        }
    }
</style>
